import React, { useState } from "react";
/**destrukturálás
 * a useState helyére
 * a props helyére
 * vigyázzni kell mást jelentenek a más színű kapcsos zárójelek
 * a kék a destrukturálás
 */
export default function Card({ frontSide, backSide }) {
  /**minden kártyának saját állapota van melyik oldalát látjuk
   * a text amivel megkapjuk a szöveget, a setText változtatja az állapotot
   */
  const [isFront, changeFace] = useState(frontSide);
  function handleClick() {
    /**oldjuk meg hogy forgatható legyen a kártya */
    changeFace((oldState) => !oldState);
  }

  const text = isFront ? frontSide : backSide;
  const sideClass = isFront ? "front" : "back";
  const classList = `flash-card ${sideClass}`;

  /**a kapcsos zárójeleken belüli kódot javascriptként értelmezi */
  return (
    <div className={classList} onClick={handleClick}>
      {text}
    </div>
  );
}
/** a flash card osztállyal stílusbeállításokat tudunk hozzá adni
 * a style.css-ben,de ide már nem kell beimportálni mert az app-ben benne van
 */
